import Component from "jx/core/comps/Component";
class FiltersData {
    constructor() {
        this.status = "all";
        this.sortBy = "id";
        this.publication = "all";
        this.engine = "all";
        this.typeProject = "Project";
        this.search = "";
        this.user = "all";
        this.domain = null;
    }
}
export default class Home extends Component {
    constructor(dataObject) {
        super(dataObject);
        this.filters = new FiltersData();
        this.editor = this.jx.editor;
        this.view = $("body");
        this.view.css("overflow", "hidden");
        this.filters = this.jx.memo.cookie.getVar("homeFilters", this.filters);
        this.menu = $(` 
		<div id="menu" style="position:fixed; width:100%; z-index:1001; background-color:${this.jx.config.app.pageBackgroundColor};border-bottom: 1px solid rgb(233, 233, 233)">
			<div class="row" style="vertical-align: bottom">
				<div class="col-xs-10">
					<button id="BTN_Projects" class="btn btn-default" title="Projets" data-action='{"action": "list", "filters":{"typeProject":"Project"}}'>PROJETS</button>

					<button id="BTN_Engines" class="btn btn-default" title="Moteurs" data-action='{"action": "list", "filters":{"typeProject":"AppEngine"}}'>MOTEURS</button>

					<button id="BTN_Models" class="btn btn-default" title="ModÃ¨les" data-action='{"action": "list", "filters":{"typeProject":"Model"}}'>MODELES</button>

					<button id="BTN_Archives" class="btn btn-default" title="Archives" data-action='{"action": "list", "filters":{"typeProject":"Archive"}}'>ARCHIVES</button>

					<button id="BTN_Components" class="btn btn-default" title="Composants" data-action='{"action": "list", "filters":{"typeProject":"Component"}}'>COMPOSANTS</button>

					<button id="BTN_Doc" class="btn btn-default" title="Doc" data-action='{"action": "list", "type": "Doc", "filters":{"typeProject":"Doc"}}'>DOC</button>

					<button id="BTN_TESTS" class="btn btn-default" title="tests"  data-access='admin,dev' data-action='{"action": "list", "type": "Project", "domain":"/projects/tests", "filters":{"typeProject":"Project", "domain":"/projects/tests"}}'>TESTS</button>

				</div>

				<div class="col-xs-1">
					<button id="BTN_Admin" class="btn btn-default btn-xs pull-right" style="border-radius:50px;margin:2px" title="Admin" data-access='admin,dev, manager' data-action='{"action": "showAdminPanel"}'><i class="fa fa-cog fa-2x" aria-hidden="true"></i></button>
				</div>
				<div class="col-xs-1">
					<span class="fa fa-user">${this.editor.user.name}</span>
					<div>(${this.editor.user.group})</div>		
				</div>
			</div> 

			<div class="row" style="vertical-align: bottom">
				<div class="col-xs-2  text-left" style="width:160px" id="choiceBar"/>
				<div class="col-xs-2  text-left"  style="width:160px" id="choiceBar1"/>
				<div class="col-xs-2  text-left"  style="width:160px" id="choiceBar3"/>
				<div class="col-xs-2  text-left"  style="width:160px" id="choiceBar4"/>
				<div class="col-xs-4"/>
			</div>
		</div>
		`);
        this.content = $(`
		<div class="fluid-container">
 			<div class="row">
			    <div class="col-xs-12" id="listContainer">
			    </div>
			</div> 
		</div>  
		`);
        let allowedPublications = this.editor.getOptionsList("publication");
        if (this.filters.publication == "all")
            this.filters.publication = allowedPublications[0];
        var publicationCB = this.cc({ type: "editionitems.SimpleComboBox", controller: this, data: { title: "Publication", options: allowedPublications, value: this.filters.publication, onChanged: evt => {
                    this.filters.publication = evt.target.value;
                    this.refreshList();
                } } });
        this.menu.find("#choiceBar").append(publicationCB.view);
        var engineCB = this.cc({ type: "editionitems.SimpleComboBox", controller: this, data: { title: "Moteur", options: "engine", value: this.filters.engine, onChanged: evt => {
                    this.filters.engine = evt.target.value;
                    this.execute({ "action": "list", "type": "Project", "domain": "/projects/milan-presse" });
                } } });
        //this.content.find("#choiceBar").append(engineCB.view);
        var statusCB = this.cc({ type: "editionitems.SimpleComboBox", controller: this, data: { title: "Status", options: "statusFilter", value: this.filters.step, onChanged: evt => {
                    this.filters.status = evt.target.value;
                    this.refreshList();
                } } });
        this.menu.find("#choiceBar1").append(statusCB.view);
        var searchBox = this.cc({ type: "editionitems.SearchBox", controller: this, data: { value: this.filters.search, onchanged: evt => {
                    this.filters.search = evt.target.value;
                    this.refreshList();
                } } });
        this.menu.find("#choiceBar").append(searchBox.view);
        searchBox.view.css("width", "200px");
        searchBox.view.css("padding-left", "0px");
        searchBox.view.css("margin-bottom", "5px");
        var optSortBy = this.cc({ type: "editionitems.SimpleComboBox", controller: this, data: { title: "Sort by", options: [
                    "Name", "Status", "Date publication ASC", "Date publication DESC",
                    "Date modification", "Date creation", "Date access"
                ],
                value: this.filters.sortBy, onChanged: evt => {
                    console.log(evt.target.value);
                    this.filters.sortBy = evt.target.value;
                    this.execute({ "action": "list", "type": "Project", "domain": "/projects/milan-presse" });
                } } });
        this.menu.find("#choiceBar4").append(optSortBy.view);
        optSortBy.view.css("width", "150px");
        var userFilter = this.cc({ type: "editionitems.SimpleComboBox", controller: this, data: { title: "User", options: "users", value: this.filters.user, onChanged: evt => {
                    console.log(evt.target.value);
                    if (evt.target.value == "me") {
                        evt.target.value = this.editor.user.name;
                    }
                    ;
                    this.filters.user = evt.target.value;
                    this.execute({ "action": "list", "type": "Project", "domain": "/projects/milan-presse" });
                } } });
        this.menu.find("#choiceBar3").append(userFilter.view);
        userFilter.view.css("width", "150px");
        this.filterBox = {
            status: statusCB,
            sortBy: optSortBy,
            user: userFilter
        };
        //--- add click on buttons. If attribute "data-action" is defined, click calls this[data-action](), else it calls this[btnID]()
        var buttons = this.menu.find(".btn");
        for (var i = 0; i < buttons.length; i++) {
            // show button depends of user group
            if ($(buttons[i]).attr("data-access") != undefined) {
                var args = $(buttons[i]).attr("data-access").split(",");
            }
            else {
                var args = ["*"];
            }
            $(buttons[i]).css("display", (this.editor.userIsGroup(...args) ? "inline" : "none"));
            if ($(buttons[i]).attr("id") == "BTN_Admin") {
                $(buttons[i]).click((event) => this.editor.showAdminPanel());
            }
            else {
                $(buttons[i]).click((event) => this.openTab($(event.currentTarget).attr("id")));
            }
        }
        this.view.append(this.menu);
        this.view.append(this.content);
        this.viewList = this.cc({ type: "editionitems.Container", render: "DOM" });
        let listContainer = this.content.find("#listContainer");
        listContainer.html(this.viewList.view);
        let top = this.menu.position().top + this.menu.outerHeight();
        listContainer.css("top", (top) + "px");
        listContainer.css("overflow", "auto");
        this.menu.css("overflow", "hidden");
        listContainer.height($(window.top).height() - top);
        $(window).on("resize", () => {
            listContainer.css("overflow", "hidden");
            let top = this.menu.position().top + this.menu.outerHeight();
            listContainer.css("top", (top) + "px");
            listContainer.height($(window.top).height() - top);
            this.jx.wait(0, () => {
                listContainer.css("overflow", "auto");
            });
        });
        this.listContainer = listContainer;
        this.openTab("BTN_Projects");
    }
    _create() {
        super._create();
        this._ready = true;
    }
    init({ onInitialised }) {
        onInitialised();
    }
    openTab(idTab, filters = null) {
        filters = filters || this.filters;
        this.activeTab = idTab;
        var dataAction = this.menu.find("#" + idTab).attr("data-action");
        if (dataAction) {
            var datObj = JSON.parse(dataAction);
            datObj.filters = datObj.filters || filters;
            this.execute(datObj);
        }
        else {
            this[idTab]();
        }
        this._refreshButtons();
    }
    _refreshFilterBox() {
        this.filterBox.status.value = this.filters.status;
        this.filterBox.sortBy.value = this.filters.sortBy;
    }
    _refreshButtons() {
        this.menu.find(".btn").removeClass("active");
        this.menu.find(".btn").css("background-color", "#FFFFFF");
        this.menu.find(".btn").css("color", "#337ab7");
        this.menu.find("#" + this.activeTab).addClass("active");
        this.menu.find("#" + this.activeTab).css("background-color", "#337ab7");
        this.menu.find("#" + this.activeTab).css("color", "#FFFFFF");
    }
    execute({ action, type, filters = {} }) {
        //console.log("execute", arguments[0]);
        filters.domain = filters.domain || null;
        this.filters = Object.assign(this.filters, filters);
        this._refreshFilterBox();
        // this.editor.host.io.emit('userAction', {username: this.editor.user.name , action, args: {filters: this.filters}});
        switch (action) {
            case 'list':
                this.refreshList();
                break;
            case 'showAdminPanel':
                this.editor.showAdminPanel();
                break;
            case 'callServiceDistant': // experimental
                //--- get list of items of type 'type'
                // this.editor.callServiceDistant("test", {"yo": "Youpy"}, evt=>{
                // 	console.log(evt);
                // });
                break;
            default:
                console.log("function not implemented : ", action);
                break;
        }
    }
    refreshList(callback = () => { }) {
        this.jx.loadingScreen.show();
        var filters = Object.assign({}, this.filters);
        if (filters.typeProject != "Project") {
            filters.status = "all";
            filters.user = "all";
            this.filterBox.status.visible = false;
            this.filterBox.user.visible = false;
        }
        else {
            this.filterBox.status.visible = true;
            this.filterBox.user.visible = true;
        }
        this.jx.memo.cookie.setVar("homeFilters", filters);
        var list = this.editor.getData({ type: filters.typeProject, filters: filters }, (event) => {
            // --- create and show content from list
            this._refreshViewList({ list: event.data });
            this.jx.loadingScreen.hide();
            callback();
        });
    }
    _refreshViewList({ list }) {
        this.viewList.removeAllChildren();
        if (list.length == 0) {
            this.viewList.addChild({ type: "Text", text: "no results with these search criteria" });
        }
        ;
        for (var i = 0; i < list.length; i++) {
            // console.log(list[i])
            this.viewList.addChild({ type: "editionitems.CatalogItem", controller: this,
                data: {
                    id: list[i].id,
                    title: list[i].title,
                    showID: list[i].type !== "Doc",
                    showStatus: list[i].type !== "Doc",
                    showPreviewBtn: list[i].type !== "Doc",
                    onclickAction: list[i].type !== "Doc" ? null : "preview",
                    type: list[i].type,
                    url: list[i].url,
                    status: list[i].infos.status.value,
                    datePublication: (list[i].projectInfo != undefined) ? list[i].projectInfo.project.datePublication : "",
                    previewIndex: (list[i].projectInfo && list[i].projectInfo.project.previewIndex) || "",
                    description: list[i].description
                } });
        }
        ;
        this.listContainer.scrollTop(0);
        return this.viewList.view;
    }
}
